<template>
  <div class="box">
    <div class="heard">
      <img src="../../../static/images/akslogo.png" alt />
    </div>
    <div class="phoneBox">
      <el-form :label-position="labelPosition" label-width="100px" class="demo-ruleForm">
        <el-form-item label="手机号码：">
          <el-input v-model="ruleForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="设置密码：">
          <el-input v-model="ruleForm.password" prop="pass"></el-input>
        </el-form-item>
        <el-form-item label="确认密码：">
          <el-input v-model="ruleForm.password" prop="pass"></el-input>
        </el-form-item>
        <el-form-item label="短信验证码：">
          <el-input
            v-model="ruleForm.code"
            placeholder="验证码"
            maxlength="6"
            @input="e => (ruleForm.code = validNumber(e))"
            style="width:260px"
          ></el-input>
          <el-button
            style="width:120px;background-color:#2ab8b3;color:#fff;border:0"
            @click="getCodes"
          >{{ count1 == 60 ? "获取验证码" : "重新获取(" + count1 + ")" }}</el-button>
        </el-form-item>
        <el-form-item>
          <p style="color:#2ab8b3;">提示:验证短信可能会有些延迟,请耐心等待。</p>
        </el-form-item>
        <el-form-item>
          <el-button
            style="background-color:#2ab8b3;color:#fff;height:49px;width:354px"
            @click="toTeacher"
          >立即注册</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="articles">
      <Articles></Articles>
    </div>
    <p class="copyright tc">
      <Footers></Footers>
    </p>
  </div>
</template>
<script>
import Footers from "../../components/common/footers";
import Articles from "../../components/articles/articles";

export default {
  components: {
    Articles,
    Footers
  },
  data() {
    return {
      labelPosition: "right",
      ruleForm: {
        phone: "",
        password: "",
        number: "",
        code: ""
      },
      count1: 60,
      t: null,
      t1: null
    };
  },
  methods: {
    toTeacher() {
      this.$router.push({ path: "/teacherRegister" });
    },
    getAuthCodes() {
      if (this.count1 == 60) {
        this.count1--;
        this.t1 = setInterval(res => {
          this.count1--;
          if (this.count1 == 0) {
            this.count1 = 60;
            clearInterval(this.t1);
          }
        }, 1000);
        let phones = this.ruleForm.phone;
        this.$post("/sso/GetVerificationVode.do", { mobile: phones }).then(
          res => {
            if (res.code == "5000") {
              //console.log(res);
              this.$message({
                message: "发送成功!",
                type: "success"
              });
            } else {
              this.$message.error(res.message);
            }
          }
        );
      }
    },
    //获取验证码
    getCodes(formName) {
      this.$refs.ruleForm.validateField("phone", validMessage => {
        if (!validMessage) {
          this.getAuthCodes();
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
.box {
  background-color: #f8f8f8;
  width: 100%;
  height: 100%;
  .heard {
    background-color: #0f8c83;
    width: 100%;
    height: 66px;
    margin-bottom: 100px;
    img {
      margin-left: 310px;
      margin-top: 7px;
    }
  }
  .phoneBox {
    width: 608px;
    height: 550px;
    background-color: #fff;
    margin: 0 auto;
    padding: 100px 60px;
    box-sizing: border-box;
  }
  .articles {
    margin-top: 100px;
    background-color: #0f8c83;

    color: #fff;
  }
}
</style>


